import React, { DetailedHTMLProps, HTMLAttributes } from 'react';
import cx from 'classnames';
import styles from '../css/components.module.css';

interface RingProps {
	radius?: number
	stroke: string
	strokeWidth?: number
}
export function Ring(props: RingProps) {

	return <>
		<svg style={{
			filter: props.radius ? `scale(${props.radius / 50})` : undefined
		}} width="50" height="49" viewBox="0 0 50 49" fill="none" xmlns="http://www.w3.org/2000/svg">
			<path d="M48 24.0597C48 36.1823 37.8301 46.1194 25.1473 46.1194C12.4644 46.1194 2.29456 36.1823 2.29456 24.0597C2.29456 11.9371 12.4644 2 25.1473 2C37.8301 2 48 11.9371 48 24.0597Z"
				stroke={props.stroke} strokeWidth={props.strokeWidth || 4}/>
		</svg>
	</>;
}

export default function Rings(props: DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement> ) {
	
	return <div {...props} className={cx(styles.rings, props.className)}>
		<Ring stroke='white' />
		<Ring stroke='#0575E6' />
	</div>;
}