import React, { useMemo } from 'react';
import cx from 'classnames';
import Rings from '../components/Ring';
import styles from '../css/about.module.css';
import Contact from './Contact';

const cardData = [
	{
		content: 'These happy socks are made from bamboo fibres. And with every pair bought, one is donated to the homeless.',
		img: '/images/socks.png'
	},
	{
		content: 'The guys that make this bedding run their factory entirely on wind power. They also use plastic-free packaging.',
		img: '/images/spread.png'
	},
	{
		content: 'This distillery makes its spirits with surplus produce from neighbouring farms.',
		img: '/images/spirits.png'
	}
];

export default function About() {
	
	const boxes = useMemo(() => {
		return cardData.map((meta, i) => {
			return <div
				key={cx('img', i)}
				className={styles.box}
				data-aos='fade-up'
				data-aos-delay={i * 100}
			>
				<img src={process.env.PUBLIC_URL + meta.img} />
				{meta.content}
			</div>;
		});
	}, []);

	return <>
		<section id='industries'>
			<div className='wrapper'>
				<h2 data-aos='fade-left'>Industries will only change if we change our buying habits.</h2>
				<h2 data-aos='fade-left'>We want to help you start discovering the businesses worth buying from.</h2>
			</div>
			<Rings className='bgRings' />
		</section>
		<section id='businesses'>
			<div className='wrapper'>
				<h2 data-aos='fade-left'>There are so many businesses making better versions of our everyday products.</h2>
				<div className={styles.boxContainer}>{boxes}</div>
				<p data-aos='fade-left'>Once we learned more, we couldn't go back to our normal brands. We started making small switches in our buying habits as we discovered more great businesses. And we won't lie, it felt great to be doing more for our people and our planet.</p>
			</div>
		</section>
		<section id='action'>
			<div className='wrapper'>
				<h1  data-aos='fade'>Ethicard is about accelerating that discovery</h1>
			</div>
		</section>
		<Contact />
	</>;

}