import React, { ReactElement, Dispatch, SetStateAction, useState, useEffect } from 'react';
import cx from 'classnames';
import { Link } from 'react-router-dom';
import { isMobile } from '../utils/auth';
import styles from '../css/header.module.css';
import { useReducer } from 'react';
import { FaBars } from 'react-icons/fa';
import Rings from './Ring';

const routes = {
	about: {
		name: 'About us',
		href: '/about'
	},
	works: {
		name: 'How it works',
		href: '/#works'
	},
	subscribe: {
		name: 'Subscribe',
		href: '#subscribe'
	}
};

export interface HeaderProps {
	setID: Dispatch<SetStateAction<string>>
}

export default function Header(props: HeaderProps): ReactElement {

	const [mobileMenu, setMenu] = useState(false);
	const [isScrolled, updateScroll] = useReducer(() => window.scrollY > 240, false);

	useEffect(() => {
		window.addEventListener('scroll', updateScroll);
		return () => window.removeEventListener('scroll', updateScroll);
	}, [updateScroll]);

	return <>
		<div className={cx(styles.header, {[styles.scrolled]: isScrolled})}>
			<div className={styles.logoWrapper}>
			</div>
			{isMobile() ? <div className={styles.menuButton} onClick={() => setMenu(!mobileMenu)}>
				<FaBars />
			</div> : <>
				<div className={styles.nameWrapper}>
					<Link 
						to='/'
						className={styles.name}>
						<b>ETHICARD</b>
						<Rings className={styles.rings} />
					</Link>
				</div>
				<div />
				<div className={styles.buttonContainer}>
					{Object.entries(routes).map(([b, meta], i) => {
						return <React.Fragment key={cx(b, i)}>
							<a
								className={styles.button}
								onClick={() => setMenu(false)}
								href={meta.href}
							>
								{meta.name}
							</a>
						</React.Fragment>;
					})}
				</div>
			</>}
		</div>
		<div className={styles.mobileMenu} style={{
			maxHeight: mobileMenu ? '100%' : '0px'
		}}>
			<div style={{ height: '26vh' }} />
			{[['home', { name: 'Home', href: '/' }] as [string, { name: string, href: string }], ...Object.entries(routes)].map(([b, meta], i) => {
				return <React.Fragment key={cx(b, i)}>
					<a
						className={styles.button}
						onClick={() => setMenu(false)}
						href={meta.href}
					>
						{meta.name}
					</a>
				</React.Fragment>;
			})}
		</div>
	</>;
}