import React, { CSSProperties, ReactNode, useCallback, useEffect, useMemo, useReducer, useState } from 'react';
import cx from 'classnames';
import styles from '../css/textcarousel.module.css';

interface CarouselProps {
	stackLength?: number
	startIndex?: number
	slides?: {
		title: string,
		description: string | ReactNode
		img: string
	}[]
	withNumbers?: boolean
}
export default function TextCarousel({
	stackLength = 3,
	startIndex = 0,
	slides = [],
	withNumbers
}: CarouselProps) {

	const [hasTriggered, trigger] = useState(false);
	useEffect(() => {
		if (!hasTriggered) return;
		let x = setTimeout(() => trigger(false), 10000);
		return () => clearTimeout(x);
	}, [hasTriggered, trigger]);

	const reduceIndex = useCallback((state: number, action: number) => {
		if (action) {
			state = action;
			trigger(true);
		}
		else state += 1;
		return state % (stackLength + 1);
	}, [stackLength, trigger]);
	const [index, setIndex] = useReducer(reduceIndex, startIndex);

	const [textIndex, setTextIndex] = useState(index);
	const currText = useMemo(() => {
		let curr = slides[textIndex];
		if (!curr) return null;
		return <>
			<h2 className={styles.body}>
				{withNumbers ? <div className={styles.numbering}>{textIndex + 1}</div> : null}
				{curr.title}
			</h2>
			{typeof curr.description !== 'string' ?
				curr.description : 
				curr.description.split('\n')
					.map((line, i) => <p className={styles.body} key={cx('p', styles.body.slice(0, 10))}>{line}</p>)
			}
		</>;
	}, [slides, textIndex]);
	useEffect(() => {
		setTextIndex(-1);
		let x = setTimeout(() => setTextIndex(index), 100);
		return () => clearTimeout(x);
	}, [setTextIndex, index]);

	useEffect(() => {
		if (hasTriggered) return;
		let x = setInterval(setIndex, 5000);
		return () => clearInterval(x);
	}, [hasTriggered, setIndex]);

	const images = useMemo(() => {
		return slides.map((s, i) => {
			return <img 
				data-aos='fade-up'
				data-aos-delay={100 * i}
				key={cx('screenshot', i)}
				className={styles.screenshot}
				id={`screenshot.${i}`}
				src={process.env.PUBLIC_URL + s.img}
				onClick={() => setIndex(i)}
			/>;
		});
	}, [setIndex]);

	const updateStack = useCallback(() => {
		let arr = Array.from(document.getElementsByClassName(styles.screenshot)) as HTMLImageElement[];
		let sorted = arr.sort((a, b) => {
			return parseInt(a.id.split('.')[1]) - parseInt(b.id.split('.')[1]);
		});
		let remainder = sorted.splice(0, index);
		sorted.push(...remainder);
		sorted.forEach((elem: HTMLImageElement, i: number) => {
			let obj: CSSProperties = {
				opacity: 1 - (1 / (stackLength + 1) * i),
				zIndex: stackLength - i + 1,
				bottom: 20 * i + 'px',
				left: window.innerWidth / 7 / stackLength * i + 'px',
				userSelect: i ? undefined : 'none',
				cursor: i ? 'pointer' : 'default'
			};
			Object.assign(elem.style, obj);
		});
	}, [index, stackLength]);
	useEffect(() => {
		updateStack();
	}, [updateStack]);

	return (
		<div className='columns'>
			<div className={cx('column-text', {[styles.shifted]: withNumbers})}>
				{currText}			
			</div>
			<div className={cx('col2', styles.imageContainer)}>
				{images}
			</div>
		</div>
	);
}